.Businesspage-Header{
  height: 20vh;
  margin-top: 0.5vh;
}
.Businesspage-Header div{
  height: 20vh;
  line-height: 18vh;
  background: url(../../img/wallhaven-lmd71r.jpg) no-repeat top;
  font-size: 5vh;
  font-weight: 600;
  color: #fff;
  text-align: center;
  text-shadow: 0 2px 2px rgba(0,0,0,.06);
  letter-spacing: 2px;
}
.Businesspage-nav{
  background-color: rgb(245, 245, 245);
  height: 4vh;
  padding: 0 15vw;
  display: flex;
  justify-content: space-between;
}
.Businesspage-nav-left{

}
.Businesspage-nav-left Button{
  font-size: 1.5vh;
}  
.Businesspage-nav-left-ziti{
  font-family: 'myFont1';
  background-image: linear-gradient(to bottom , rgb(255, 156, 14), rgb(240, 123, 13));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.Businesspage-nav-right{
  font-size: 1.5vh;
  height: 4vh;
}
.Businesspage-nav-right div{
font-size: 1.5vh;
height: 4vh;

}
:where(.css-dev-only-do-not-override-18iikkb).ant-layout-header{
  padding: unset;
}