@font-face {
  font-family:'myFont1' ;
  src: url('../../fontfamily/ZCOOLXiaoWei-Regular.ttf');
}
ul,li{
  margin: 0;
  padding: 0;
list-style: none;
}
.header-top{
  background-image: url("../../img/zhjy-blue-pc1x.png");
  background-repeat: no-repeat;background-position: center center;    background-size: auto 50px;
  cursor: pointer;
  overflow: hidden;display: block;
  height: 5vh;
  background-color: #1D61EC;
}
 a{
  color: rgb(153, 153, 153);
}
.header-right{
line-height: 11vh;
}
.header-left{
  float: 'left';
  height: 11vh;
  width: 40vh;
  line-height: 11vh;
  font-size: 5vh;
  margin-left: 1vh;
  font-family: 'myFont1';
  background-image: linear-gradient(to bottom , rgb(255, 156, 14), rgb(240, 123, 13));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.underscore:hover{
  text-decoration: underline;
}
:where(.css-dev-only-do-not-override-18iikkb).ant-modal .ant-modal-title{
  font-size: 35px;
}
.home-content{
  background-color: rgba(252, 166, 44, 0.8);
  height: 10vh;
}

.home-Content-Col-a{
  height: 10vh;
  line-height: 10vh;
  text-align: center;
  font-size: 3vh;
  color: rgb(255, 255, 255);

}
.home-Content-Col-a:hover{
  color: rgb(255, 255, 255);
}
.home-Content-Col:hover{
  background-color: rgb(255, 159, 24);
  transition: 1s;
  cursor: pointer;
}
.home-content-2-1{
  background-color: rgb(255, 255, 255);
  margin-right: 9.2vh;
  border-radius: 10px;
}
.home-content-2-1 div ul li  {
height: 8vh;
padding-left: 4vh;
}
.home-content-2-1 div ul li:hover{
background-color: rgb(242, 244, 243);
cursor: pointer;
}
.home-content-2-1 div ul li::after {
  content: "";
  display: inline-block;
  width: 1vh;
  height: 8vh;
  background: url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100% 100%;
  float: right;
  }
.home-content-2-1 div ul li a {
  font-size: 3vh;
  line-height: 8vh;
  color: rgb(19, 27, 24);
  }
.home-content-2-2{
  height: 50vh;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  margin-right: 7.1vh;
}.home-content-2-3{
  border-radius: 10px; 
  background-color: #ffffff;
  overflow-y: auto; /* 只在垂直方向上显示滚动条 */
  overflow-x: hidden; /* 在水平方向上隐藏滚动条 */
  height: 50vh;
  width: 30vh;
}
.slick-prev{
  z-index: 10;
  left: 0;
}
.slick-prev:before{
  font-size: 5vh;
}
 .slick-next{
  z-index: 10;
  right: 2.8vh;
 }
 .slick-next:before{
  font-size: 5vh;
}
.mycolor:hover{
    color: rgb(255, 156, 14) !important;
}
.gaokao-know{
  margin: 5vh 0vh;
  background-color: #fff;
  padding: 5vh 30vh 3vh 30vh;
  height: 90vh;
}
.gaokao-know-title{
  margin-left: 3vh;
  font-size: 4vh;
  font-weight: bold;
}
.gaokao-know-content1{
  margin-top: 5vh;
  display: table;
}
.gaokao-know-content2{
  margin-top: 5vh;
  display: table;
}
.gaokao-know-content-cc{
  margin: 0 4vh;
  float: left;
}
.gaokao-know-content-cc a:hover{
  color: rgb(255, 156, 14) !important;
  text-decoration: underline;
}  

.policy-information-title{
  margin-left: 3vh;
  font-size: 4vh;
  font-weight: bold;
}

.policy-information{
  margin: 5vh 0vh;
  padding: 5vh 30vh 3vh 30vh;
  height: 50vh;
}
.policy-information-content{
  margin-top: 5vh;
}

:where(.css-dev-only-do-not-override-18iikkb).ant-float-btn-primary .ant-float-btn-body{
  background-color: rgb(255, 156, 14) ;
}
:where(.css-dev-only-do-not-override-18iikkb).ant-float-btn-primary .ant-float-btn-body:hover{
  background-color: rgb(253, 194, 110) ;
}
.gaokao-know .ant-card-head-title{
  color:rgb(255, 156, 14) ;
}
.policy-information  .ant-card-head-title{
  color:rgb(255, 156, 14) ;
}

.home-content-2-3::-webkit-scrollbar-track {
  background: #c52929 !important;
}
.myFloatButton {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.myFloatButton.show {
  opacity: 1;
}