.Schoolprofile {
  margin: 4vh 12vw 2vh 12vw;
  display: flex;
  justify-content: space-between;
}
.Schoolprofile-left {
  width: 50vw;
}
.Schoolprofile-right {
  width: 23vw;
}
.Schoolprofile_information {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.Schoolprofile_title {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 2vh 0 2vh 0;
  border-left: 2px solid #ffa652;
  padding-left: 1vw;
}
.Schoolprofile_information_content1 {
  margin-top: 2vh;
}
.Schoolprofile_information_content-div {
  height: 12vh;
  width: 11vw;
  background-color: #fff;
  box-shadow: 0 0 13px #e6e6e6;
  border-radius: 10px;
  padding: 1vh 1vw 1vh 1vw;
  /*   box-sizing: border-box;使得padding不会撑开盒子 */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.Schoolprofile_information_content-div span {
  font-size: 1.7rem;
  font-weight: 500;
}
.Schoolprofile_information_content-div img {
  height: 3.5vh;
  width: 1.7vw;
}
.Schoolprofile_information_content2 {
  box-shadow: 0 0 13px #e6e6e6;
  margin-top: 2vh;
  height: 25vh;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  box-sizing: border-box;
  padding: 2vh 2vw 2vh 2vw;
}
.Schoolprofile_information_content2 img {
  height: 3.5vh;
  width: 1.7vw;
}
.Schoolprofile_intro-content {
  box-shadow: 0 0 13px #e6e6e6;
  margin-top: 2vh;
}
.Schoolprofile-right-close-tips::before{
  content: "✱"; color: red; margin: 0px 5px 0px 0px;
}
.Schoolprofile-right-close-views{
  border-radius: 10px;
}
.Schoolprofile-right-close{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}