.Collegeview-collegebox{
  background: linear-gradient(131deg, rgb(255, 102, 0) 0%, rgb(255, 187, 72) 100%);
  background-size: cover;
  height: 45vh;
  margin: 6vh 12vw 2vh 12vw;
  padding: 5vh 5vh 0vh 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between
}
.Collegeview-collegebox-top{
  display: flex;
  justify-content: space-evenly;
}
.Collegeview-collegebox-top-right{
  height: 100%;
  width: 45vw;
}
.Collegeview-collegebox-top-left{
  padding: auto 0;
}

.Collegeview-collegebox-bottom li{
height: 6vh;
width: 10vw;
display: flex;
align-items: center;
color: white;
justify-content: center;
background-color: #ffa652;
}
.Collegeview-collegebox-bottom li:hover{
  color:rgb(255, 156, 14) ;
  background-color: #fff;
  cursor: pointer;
}
.Collegeview-Affix{
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,.1);
  height: 0vh;
  transition: height 0.3s ease-out;
  overflow: hidden;
   display: flex;
   width: 100vw;
 /* justify-content: space-between; */
  padding-left: 12vw;
  padding-right:  0vw;
}
.Collegeview-Affix.show {
  height: 8vh; /* 你可以根据需要调整这个值 */
}
.Collegeview-Affix-left{
  display: flex;
  margin-right: 45vw;
  width: 20vw;
}
.Collegeview-Affix-left li{
  height: 7.5vh;
  width: 15vw;
  display: flex;
  align-items: center;
  color: black;
  justify-content: center;
  background-color: #fff;
  font-size: larger;
  position: relative;
}
.Collegeview-Affix-left li:hover{
  color:rgb(255, 156, 14) ;
  background-color: #fff;
  cursor: pointer;
}
.Collegeview-Affix-left li:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: #ffa652;
  transition: all 0.5s ease;
  transform: translateX(-50%);
}

.Collegeview-Affix-left li:hover:before {
  width: 100%;
}
.Collegeview-Affix-right{
  width: 25vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.head-nav-fix_hotIcon__3jwMZ{
  position: absolute;
  top: 0vh;
  right: 6vh;
  width: 4vh;
  height: 4vh;
}